import getUserInfo from "../../components/common/getUserInfo.vue"
import indexListItem from "../../components/index/index-list-item.vue"
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'

import {mapState} from "vuex"
export default {
	data() {
		return {
			isLike:false,
			count:0,  // 用来记录无限列表的分页加载
			total:0, // 总记录数
			status: 'loading', // loading noMore
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			}
		}
	},
	computed:{
		...mapState(['recommend'])
	},
	methods: {
		gotoSearch(){
			uni.navigateTo({
				url:'../search/search'
			})
		}
	},
	onReady() {
		wx.cloud.callFunction({ 
		  // 要调用的云函数名称
		  name: 'getAllCount',
		  success: res => { 
			this.total = res.result.total
			console.log(this.total);
		  },
		  fail: err => {
			  console.log(err);
			
		  }
		})	
	},
	onReachBottom(){
		this.count++
		console.log(this.count);
		this.status = 'loading'
		if(this.count >= this.total/10){
			setTimeout(()=>{
				this.status = 'noMore'
			},500)
			return
		}
		this.$store.dispatch('getRecommend',this.count)
	},
	components:{
		getUserInfo,
		indexListItem,
		uniLoadMore
	}
}
